import request from "@/utils/request";
export default {
  data() {
    return {
      navList: {},

    };
  },
  methods: {
    async getNavList() {
      await request
      .get('/navi')
      .then((res) => {
        console.log(res);
        this.navList = res.data.data;
      })
      .catch((err)=>{
        console.log(err);
      }
    )
   },
  },
  async mounted() {
    this.getNavList();
  }
}
